#project {
    display: flex;

    .arrow-top {
        position: absolute;
        top: 10%;
        animation: arrowtop 1.5s infinite;

        img {
            width: 4rem;
            height: 4rem;
            cursor: pointer;
        }
    }

    @media (max-width : 760px) {
        .arrow-top {
            img {
                width: 3rem;
                height: 3rem;
            }
        }

    }

    .ctn-projects {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        .ctn-project {
            position: relative;
            width: 30rem;
            height: 15rem;
            transition: 0.5s;
            .project {
                transition: 0.5s;
                img {
                    cursor: pointer;
                    width: 100%;
                    height: 100%;
                }
                &::after {
                    content: "";
                    position: absolute;
                    background-color: $blue;
                    opacity: 0.7;
                    width: 0%;
                    height: 100%;
                    top: 0;
                    transition: 0.5s;
                    cursor: pointer;
                }
            }

            &:hover {
                .project {
                    transition: 0.5s;
                    &::after {
                        width: 100%;
                        left: 0;
                        right: 0;
                        margin: auto;
                        transition: 0.5s;
                    }
                }
            }
        }
    }

    @media (max-width: 1460px) {
        .ctn-projects {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding-top: 6rem;
            max-width: 30rem;
            width: calc(50%);
            .ctn-project {
                width: 100%;
                height: auto;
                &:first-child {
                    margin-bottom: 1rem;
                }
            }
        }
    }

    @media (max-width: 760px) {
        .ctn-projects {
            width: calc(100% - 2rem);
            .project {
                background-color: rgb(230,230,230);
                p {
                    display: block;
                    font-family: $body;
                    text-align: center;
                    margin: 0;
                }
            }
        }
    }

    #projects.content {
        padding: 0;
        margin-left: 2rem;
        width: 200%;
        height: 100%;
        display: flex;
        // background-color: $blue;
        .view-project {
            width: 50%;
            height: 100%;
            margin-right: 2rem;
            flex-shrink: 0;

            .arrow_left {
                position: absolute;
                top: 2rem;
                left: 4rem;
                cursor: pointer;
                img {
                    width: 4rem;
                }
            }

            .vp-content {
                position: absolute;
                left: calc(100px + 3rem + 2rem);
                right: calc(100%/3);
                height: 100%;
                padding: 1rem 0;
                font-family: $title;
                display: flex;
                flex-direction: column;

                h2 {
                    position: relative;
                    font-size: 2.5rem;
                    display: table;
                    margin: 0.5rem 0;
                    &::after {
                        content: "";
                        position: absolute;
                        width: 100%;
                        height: 3px;
                        background-color: $blue;
                        bottom: 0;
                        left: 0;
                    }
                }

                .subtitle {
                    font-size: 0.9rem;
                    font-family: $subtitle;
                }

                .techno {
                    font-size: 1.5rem;
                }

                p {
                    font-family: $body;
                }

                .img-devices {
                    margin: auto;
                    max-width: 35rem;
                    width: 100%;
                }

                .bottom {
                    font-size: 1.5rem;
                    flex-grow: 1;
                    display: flex;
                    flex-direction: row;
                    align-items: flex-end;
                    justify-content: space-between;
                    .c-d {
                        .author {
                            font-family: $off;
                            font-weight: 700;
                            font-size: 1.5rem;
                        }
                    }
                }
            }

            .vp-img {
                width: calc(100%/3);
                height: 100vh;
                position: absolute;
                top: calc(-1rem - 3px);
                right: calc(-1rem - 3px);

                .inner-img {
                    width: 100%;
                    height: 100%;
                    overflow: hidden;
                    position: relative;;

                    &::after {
                        content: '';
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        z-index: 1000;
                        top: 0;
                        left: 0;
                        background-color: $blue;
                        opacity: 0.7;
                    }

                    img {
                        position: absolute;
                        top: -10rem;
                        min-width: 100%;
                        min-height: 100%;
                    }

                    .white-border {
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        z-index: 10000;
                        padding: 1rem;
                        padding-left: 0;
                        .true-border {
                            border: 3px solid white;
                            border-left: none;
                            width: 100%;
                            height: 100%;
                        }
                    }
                }

            }
        }
    }

    @media (max-width: 760px) {
        #projects.content {
            .view-project {
                .arrow_left {
                    bottom: inherit;
                    top: 1rem;
                    left: 1rem;
                    z-index: 9999;

                    img {
                        filter: brightness(0) invert(1);
                    }
                }

                .vp-content {
                    width: 100%;
                    left: 0;
                    top: 8rem;
                    padding: 1rem;
                    overflow: scroll;
                    height: calc(100vh - 11rem - 3px);
                    h2 {
                        &::after {
                        }
                    }

                    .subtitle {
                    }

                    .techno {
                    }

                    p {
                    }

                    .bottom {
                        margin-top: 7rem;
                        align-items: center;
                        .c-d {
                            .author {
                            }
                        }
                    }
                }

                .vp-img {
                    width: 100vw;
                    height: 8rem;
                    left: calc(-1rem - 3px);
                    top: calc(-1rem - 3px);
                    z-index: 9998;
                    .inner-img {
                        img {
                            top: 0;
                            width: 100%;
                            height: auto;
                            transform: translateY(-3rem);
                        }

                        .white-border {
                            padding: 1rem;
                            padding-bottom: 0;
                            .true-border {
                                border-bottom: none;
                                border-left: 3px solid white;
                            }
                        }
                    }

                }
            }
        }
    }
}
